import React, { useState } from 'react'

export const AddToDo = (props) => {

    const [Title, setTitle] = useState("");
    const [desc, setdesc] = useState("");

    const submit = (e) => {
        e.preventDefault()

        if (Title === "" || desc === "") {
            alert("You cannot leave any field Empty")
        } else {
            var today = new Date(),
                date = today.getDate() + '-' + (today.getMonth() + 1) + '-' + today.getFullYear() + ' ' + today.getHours() + ':' + today.getMinutes() + ':' + today.getSeconds();
            props.addToDo(Title, desc, date);
            setTitle("");
            setdesc("");
        }
    }

    return (
        <div className="container">
            <form onSubmit={submit}>
                <div className="mb-3 my-3">
                    <label htmlFor="title" className="form-label">ToDo Title</label>
                    <input type="text" value={Title} onChange={(e) => { setTitle(e.target.value) }} className="form-control" id="title" placeholder="Enter a ToDo Title" />
                </div>
                <div className="mb-3">
                    <label htmlFor="desc" className="form-label">ToDo Description</label>
                    <textarea className="form-control" onChange={(e) => { setdesc(e.target.value) }} value={desc} id="desc" rows="3" placeholder="Enter a ToDo Description"></textarea>
                    <button type="submit" className="btn btn-success btn-sm my-3">Add</button>
                </div>
            </form>
        </div>
    )
}
