import React from 'react'

export const ToDo = (props) => {
    let index = 0;
    return (
        <div className="accordion container" id="accordionExample">
            {props.todos.length !== 0 ?props.todos.map((todo, index) => {
                return (
                    <div className="accordion-item"  key={++index}>
                        <h2 className="accordion-header" id="headingOne">
                            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target={"#collapseOne" + index} aria-expanded="true" aria-controls={"collapseOne" + index}>
                                {todo.title}
                            </button>
                        </h2>
                        <div id={"collapseOne" + index} className="accordion-collapse collapse" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
                            <div className="accordion-body">
                                {todo.desc}
                                <button onClick={()=>{props.onDelete(todo)}} className="btn btn-danger btn-sm mx-3 float-right">Delete</button>
                                {todo.time}
                            </div>
                        </div>
                    </div>
                )
            }) : "No Todos to Display."}
        </div>
    )
}
